.borderGameProfile {
    padding: 2px;
    height: 300px;
    border-radius: 11px;
    background-image: linear-gradient(45deg, #19bf23ff, #0467b7ff);
    margin: 30px;
}
.gameProfile {
    margin: auto;
    width: 500px;
    height: 100%;
    border-radius: 10px;
    overflow: hidden;
    position: relative;
}
.profileImage {
    width: 100%;
    height: 100%;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
}
.gameLink a  {
    color: #7fbbfff2;
    font-size: 30px;
    padding: 10px 15px 10px 15px;
    /* border-radius: 10px; */
    background: none;
    border: none;
    text-decoration: underline;
    width: 100%;
}
.profileDesc {
    position: absolute;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.8);
    width: 100%;
    height: 120px;
    transition: height .2s;
    border-top: 4px solid;
}
.gameTitle p {
    margin-top: 0;
    margin-bottom: 0;
}
.gameTitle {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: -22px;
    width: 60%;
    background-color: #f2f2f2;
    color: black;
    font-size: 20px;
    padding: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 40px;
    border-radius: 10px;
}
.gameLink {
    position: absolute;
    bottom: 5px;
    width: 300px;
    text-align: center;
}
.gameDesc {
    text-align: center;
    opacity: 0;
    transition: opacity .2s;
    margin: 40px 20px 0 20px;
}
.profileImage img {
    transition: scale .2s;
}
.borderGameProfile:hover .profileDesc {
    height: 250px;
}
.borderGameProfile:hover .gameDesc {
    opacity: 100;
}
.borderGameProfile:hover .profileImage img {
    scale: 1.1;
}